body {
  margin: 0;
  font-family: -apple-system,
    BlinkMacSystemFont,
    'Segoe UI',
    'Roboto',
    'Oxygen',
    'Ubuntu',
    'Cantarell',
    'Fira Sans',
    'Droid Sans',
    'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro,
    Menlo,
    Monaco,
    Consolas,
    'Courier New',
    monospace;
}

.headerPartners {
  text-align: center;
}
.contnr {
  display: flex;
  justify-content: space-between;
}

.logo {
  padding: 0.5rem;
  width: 55%;
  margin-top: auto;
  margin-bottom: 0;
}
.imgLogo {
  width: 100%;
}
.contactList {
  list-style: none;
  display: flex;
  flex-wrap: wrap;
}
.phone {
  font-weight: 600;
  text-align: center;
}
.contacts {
  width: 28%;
  padding: 0.5rem;
}
.elemnt {
  display: block;
  width: 50%;
  padding: 0.7rem;
  filter: grayscale(
    0.8
  );
}
.elemnt:hover {
  filter: grayscale(
    0
  );
}
.elementImg {
  width: 100%;
}
.navbar {
  font-size: 120%;
}
.footer {
  height: 4rem;
  display: flex;
  justify-content: flex-end;
  align-items: flex-end;
  background-color: rgb(
    235,
    235,
    235
  );
  position: absolute;
  width: 100%;
}
.copyright {
  display: block;
  font-size: 150%;
  padding-right: 0.4rem;
}
